
/*Nav*/
nav {
  // float: right;
  @include bp(mobile_large){
    @include flex;

    @include flex_align_items--center;
  }
}
nav a {
  transition: .35s linear;
  padding: 0 10px;
  font-weight: 500;
  line-height: 130px;
  font-size: 1.5em;
  color: white;
  // text-align: right;
  @include bp(mobile_large){
      padding-left: em(5px);
      padding-right: em(5px);
  }
}
nav a:last-child {
  padding-right: 0;
}
