sc-player > .wrapper {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
sc-player > p.small {
  font-size: 0.8em;
}
sc-player > a {
  color: #9c3538;
  &:hover{
    color: #bf3d3f;
  }
}

a.sc-player,
.button {
  display: inline-block;
  background: #bf3d3f;
  background: rgba(19, 19, 19, 0.14);
  border-radius: 4px;
  padding: 1em 1.5em;
  text-align: center;
  text-decoration: none;
  color: #f8f8f8;
  font-size: 1.5em;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
a.sc-player:hover,
.button:hover {
  background: #9c3538;
  background: rgba(19, 19, 19, 0.24);
  color: #f8f8f8;
}
.main {
  margin-bottom: 4em;
}
.transition {
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

